import {Component, Input, EventEmitter, Output, OnInit} from '@angular/core';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {AddedCoffeeHouseComponent} from 'app/core/dialogs/added-coffee-house/added-coffee-house.component';
import {HttpService} from '../../services/http.service';
import {ConfigsService} from '../../configs.service';
import {ToastrService} from 'ngx-toastr';
import {CoffeeHousesService} from '../../services/coffee-houses.service';
import {ConfirmDialogComponent} from '../../dialogs/confirm-dialog/confirm-dialog.component';
import {MatDialog} from '@angular/material';

@Component({
	selector: 'app-item-edit-btns',
	templateUrl: './item-edit-btns.component.html',
	styleUrls: ['./item-edit-btns.component.scss']
})
export class ItemEditBtnsComponent implements OnInit {
	@Input() item;

	constructor(private modalService: NgbModal, private httpService: HttpService,
				private configs: ConfigsService, private toastrService: ToastrService,
							private coffeeHousesService: CoffeeHousesService,
							private dialog: MatDialog) {}

	ngOnInit() {
	}

	edit() {
		const modalRef = this.modalService.open(AddedCoffeeHouseComponent);
		modalRef.componentInstance.item = this.item;
	}

	remove() {
		const dialogRef = this.dialog.open(ConfirmDialogComponent, {
			data: {
				header: 'Delete',
				message: 'Are you sure want to delete the coffee house?',
				yes: 'Delete',
				no: 'Cancel'
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			if (result) {
				this.httpService.delete(this.configs.urlRequest.updateCoffeeHouse + this.item._id, {})
					.subscribe(response => {
						this.toastrService.success('Removed successfully!');
						this.coffeeHousesService.onChanged.emit(response);
					});
			}
		});
	}
}



