import {Router, ActivatedRoute, Params} from '@angular/router';
import {OnInit, Component} from '@angular/core';
import {FormControl, Validators} from '@angular/forms';
import {OwnerService} from '../../core/services/owner.service';
import {ToastrService} from 'ngx-toastr';
import {AdminService} from '../../core/services/admin.service';
import {Observable} from 'rxjs/Observable';

@Component({
	selector: 'app-reset-password',
	templateUrl: './reset-password.component.html',
	styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
	input: any;

	passwordFormControl = new FormControl('', [
		Validators.required,
		Validators.minLength(6)
	]);

	confirmPasswordFormControl = new FormControl('', [
		Validators.required,
		Validators.minLength(6)
	]);

	constructor(private activatedRoute: ActivatedRoute,
							private ownerService: OwnerService,
							private admin: AdminService,
							private router: Router,
							private toastrService: ToastrService
	) {
		this.input = {
			confirmPassword: '',
			password: '',
			id: '',
			verificationCode: ''
		};
	}

	ngOnInit() {
		this.activatedRoute.queryParams.subscribe((params: Params) => {
			console.log(params);
			this.input.verificationCode = 'code' in params ? params['code'] : '';
			this.input.id = 'id' in params ? params['id'] : '';
		});
	}

	hasError() {
		return !this.passwordFormControl.valid
			|| !this.confirmPasswordFormControl.valid ||
			this.passwordFormControl.value !== this.confirmPasswordFormControl.value;
	}

	Update(data) {
		this.admin.resetPassword(data)
			.catch((error: any) => {
				if (error.message === '') {
					// this.toastrService.error('Activation code not found');
				} else {
					this.toastrService.error(error.message);
				}
				return Observable.throw(error);
			})
			.subscribe((response) => {
				this.router.navigate(['/']);
			});
	}
}



