import {Component, OnInit} from '@angular/core';
import {AdminService} from '../../core/services/admin.service';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import 'rxjs/add/operator/catch';
import {ToastrService} from 'ngx-toastr';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import {TranslateService} from '@ngx-translate/core';

@Component({
	selector: 'app-forgot-password',
	templateUrl: './forgot-password.component.html',
	styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
	forgotForm: FormGroup;
	emailPlaceholder = 'Your e-mail address';

	constructor(private adminService: AdminService,
							private toastrService: ToastrService,
							private translateService: TranslateService,
							private fb: FormBuilder) {
		this.createForm();
		translateService.get('text.your_email').toPromise().then(data => {
			this.emailPlaceholder = data;
		});
	}

	ngOnInit() {
	}

	createForm() {
		this.forgotForm = this.fb.group({
			email: ['', Validators.compose([Validators.required, Validators.email])], // <--- the FormControl called "name"
		});
	}

	cancel() {
		window.location.replace('/login');
	}

	onSubmit(form) {
			this.adminService.forgotPassword(form.get('email').value)
				.catch((error: any) => {
					if (error.message === 'No such account found') {
						this.translateService.get('text.account_not_found')
							.subscribe((res: string) => {
								this.toastrService.error(res);
							});
					} else {
						this.toastrService.error(error.message);
					}
					return Observable.throw(error);
				})
				.subscribe(result => {
					form.get('email').reset('');
					if (result.message === 'Email with reset password link was sent on your email') {
						this.translateService.get('text.reset_password_link_was_sent')
							.subscribe((res: string) => {
								this.toastrService.success(res);
							});
					} else {
						this.toastrService.success(result.message);
					}
				});
	}
}



