import {Injectable} from '@angular/core';
import {Http, Response, Headers, URLSearchParams, RequestOptions, ResponseContentType} from '@angular/http';
import { HttpClient } from '@angular/common/http';
import {Router} from '@angular/router';
import {ConfigsService} from 'app/core/configs.service';
import {LogoutService} from 'app/core/services/logout.service';

import {ToastrService} from 'ngx-toastr';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';



// For queries now used OLD 'Http' lib, from '@angular/http' - this is bad, and gradually rewrite this service for using new HttpClient
@Injectable()
export class HttpService {

	constructor(public router: Router,
				private http: Http, private httpClient: HttpClient,
				private config: ConfigsService,
				public logout: LogoutService,
				private toastrService: ToastrService
	) {
	}

	private credential = localStorage.getItem('Authorization');

	// http://54.72.192.165:8031/api/v1/ Base Url
	login(url: string, data?: any) {
		const headers = {
			'Content-Type': 'application/json',
		};

		const self = this;
		return this.httpClient.post(this.config.baseUrl + url, data || {}, { headers: headers })
			.catch((error: any) => {
				self.errorHandler(error);
				return Observable.throw(error);
			});
	}

	getFile(url: string, data?: any) {
		const self = this;

		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});

		const options = new RequestOptions({
			responseType: ResponseContentType.Blob,
			headers: headers
		});

		return this.http.get(this.config.baseUrl + url, options)
			.map((response) => {
				return response.json();
			})
			.catch((error: any) => {
				self.errorHandler(error);
				return Observable.throw(error);
			});
	}

	public get(url: string, options?: any) {
		const self = this;

		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});

		return this.http.get(this.config.baseUrl + url, options ? options : {headers})
			.map((response) => {
				return response.json();
			})
			.catch((error: any) => {
				self.errorHandler(error);
				return Observable.throw(error);
			});
	}

	public post(url: string, data?: any, header?) {
		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});

		return this.http.post(this.config.baseUrl + url, data || {}, {headers: header || headers})
			.map((response) => {
				return response.json();
			})
			.catch((error: any) => {
				// self.errorHandler(error);
				return Observable.throw(error.json());
			});
	}

	public put(url: string, data?: any, header?) {
		// const headers = new Headers(header ? header : this.config.headersDefault);
		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});
		const self = this;

		return this.http.put(this.config.baseUrl + url, data || {}, {headers: headers})
			.map((response) => {
				return response.json();
			}).catch((error: any) => {
				// self.errorHandler(error);
				return Observable.throw(error.json());
			});
	}

	public delete(url: string, data?: any, header?) {
		// const headers = new Headers(header ? header : this.config.headersDefault);
		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});
		const self = this;

		return this.http.delete(this.config.baseUrl + url, {headers: headers})
			.map((response) => {
				return response.json();
			})
			.catch((error: any) => {
				self.errorHandler(error.json());
				return Observable.throw(error);
			});
	}


	errorHandler(error) {
		console.error(error);
		if (error.status === 401) {
			this.toastrService.error(error.message || error);
			// this.logout.logout();
		} else {
			this.toastrService.error(error.message || error);
		}
	}
}



