import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';
import {RequestOptions} from '@angular/http';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {startWith} from 'rxjs/operators/startWith';
import {map} from 'rxjs/operators/map';
// import {NgForm, FormControl, FormGroup} from '@angular/forms';
import {NgbActiveModal, NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';

import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {CoffeeHousesService} from '../../services/coffee-houses.service';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {GlobalDataService} from '../../services/global-data.service';
import {AdminService} from '../../services/admin.service';


@Component({
	selector: 'app-added-coffee-house',
	templateUrl: './added-coffee-house.component.html',
	styleUrls: ['./added-coffee-house.component.scss']
	// providers: [{provide: MATERIAL_COMPATIBILITY_MODE, useValue: true}]
})

export class AddedCoffeeHouseComponent implements OnInit {
	@Input() item: object;
	@Input() isReadOnly = false;
	@Input() popupOpened: boolean;
	@Input() popupOpened2: boolean;
	public isCreate = true;

	spinner = false;
	userType: string;
	// options = ['one', 'two', 'three'];
	// myControl: FormControl = new FormControl();
	// selectedUser = {name: ''};

	selectedOwner = {name: ''};
	coffeeHouseImages = [];
	stateOwnerCtrl: FormControl;
	filteredOwnerStates: Observable<any[]>;
	users = [];
	baners = [];
	owners = [];

	data: any = {
		_id: localStorage.token,
		avatarUrl: '/assets/img/coffee-house.jpg',
		admins: [],
		name: '',
		coins: 0,
		description: '',
		address: '',
		location: [
			48.609024,
			22.28984200000002
		],
		wifi: {
			bssid: '',
			ssid: '',
			wifiPassword: '',
			welcomeMessage: '',
		},
		socials: {
			facebook: '',
			instagram: '',
			twitter: '',
			google: '',
			pinterest: '',
		},
		bannerUrls: [],
		owner: localStorage.token
	};

	constructor(public activeModal: NgbActiveModal,
							private toastrService: ToastrService,
							private httpService: HttpService,
							private configs: ConfigsService,
							private globalDataService: GlobalDataService,
							private coffeeHousesService: CoffeeHousesService,
							private admin: AdminService) {
	}

	ngOnInit() {
		const self = this;

		const userData = this.globalDataService.getUserData();
		// this.fullName = userData['admin']['name'] || '';
		this.userType = userData['admin']['type'] || '';

		if (this.userType !== 'OWNER') {
			this.getOwners();
		}


		this.stateOwnerCtrl = new FormControl();


		if (this.item) {
			this.isCreate = false;
			this.spinner = true;

			const options = new RequestOptions({
				headers: this.configs.headersDefault
			});

			self.httpService.get(self.configs.urlRequest.getCoffeeHouse + '/' + this.item['_id'], options)
				.subscribe(response => {
					self.data = {
						_id: response._id || localStorage.token,
						avatarUrl: response.avatarUrl || self.data.avatarUrl,
						admins: response.admins || [],
						name: response.name || '',
						coins: response.coins || '',
						description: response.description || '',
						address: response.address || '',
						location: response.location || [
							48.609024,
							22.28984200000002
						],
						wifi: {
							bssid: response.wifi ? response.wifi.bssid : '',
							ssid: response.wifi ? response.wifi.ssid : '',
							wifiPassword: response.wifi ? response.wifi.wifiPassword : '',
							welcomeMessage: response.wifi ? response.wifi.welcomeMessage : '',
						},
						socials: {
							facebook: response.socials ? response.socials.facebook : '',
							instagram: response.socials ? response.socials.instagram : '',
							twitter: response.socials ? response.socials.twitter : '',
							google: response.socials ? response.socials.google : '',
							pinterest: response.socials ? response.socials.pinterest : '',
						},
						bannerUrls: response.bannerUrls || []
					};

					self.spinner = false;

					this.selectedOwner = response.owner;
				});
		}
	}

	removeAdditionalPhoto(index) {
		this.data.bannerUrls.splice(index, 1);
	}

	changeAdditionalPhoto() {
		if (this.isReadOnly) {
			return;
		}

		this.popupOpened2 = true;
	}

	changeMainPhoto() {
		if (this.isReadOnly) {
			return;
		}

		this.popupOpened = true;
	}

	addAdditionalPhoto() {
		if (this.isReadOnly) {
			return;
		}

		this.popupOpened2 = true;
	}

	createAdminRequest(coffeeHouseId, user) {
		console.log(coffeeHouseId, user._id);
		const requestData = {
			coffeeHouseID: coffeeHouseId,
			userID: user._id
		};

		this.httpService.post(this.configs.urlRequest.adminRequest, requestData)
			.subscribe(response => {
				console.log(response);
				this.toastrService.success('Request sent!');
			});
	}

	filterOwnerStates(name: any) {
		return this.owners.filter(state =>
			state.name.toLowerCase().indexOf(name.toLowerCase()) === 0);
	}

	displayFn(user: any): string {
		return user ? user.name : user;
	}

	newPosition(pos) {
		this.data.location = pos;
	}

	getOwners() {
		this.admin.getAllOwners().subscribe(data => {
			this.owners = data;

			this.filteredOwnerStates = this.stateOwnerCtrl.valueChanges
				.pipe(
					startWith({} as any),
					// map(state => state ? this.filterStates(state) : this.users.slice())
					map(owner => owner && typeof owner === 'object' ? owner['name'] : owner),
					map(name => name ? this.filterOwnerStates(name) : this.owners.slice())
				);
		});
	}


	closePopup(popupOpened: boolean, popupOpened2: boolean): void {
		this.popupOpened = popupOpened;
		this.popupOpened2 = popupOpened2;
	}

	closePosterPopup(popupOpened2: boolean) {
		this.popupOpened2 = popupOpened2;
	}

	sendCoffeeHouseFun() {
		this.spinner = true;
		if (this.isCreate) {
			this.createCoffeeHouseFun();
		} else {
			this.updateCoffeeHouseFun();
		}
	}

	setNewAvatar(url) {
		this.data.avatarUrl = url;
	}

	addPoster(url) {
		this.data.bannerUrls.push(url);
	}

	createCoffeeHouseFun() {
		const self = this;
		// TODO: EDIT THIS!!!
		this.data.bannerUrls = [];
		this.data.owner = this.selectedOwner['_id'] || localStorage.token;

		this.httpService.post(this.configs.urlRequest.createCoffeeHouse, JSON.stringify(this.data))
			.subscribe(data => {
				self.coffeeHousesService.onChanged.emit(data);
				self.spinner = false;
				self.toastrService.success('Created');
				self.activeModal.close(AddedCoffeeHouseComponent);
			});
	}

	updateCoffeeHouseFun() {
		const self = this;
		// this.data.admins = this.selectedAdmin && this.selectedAdmin['_id'] ? [this.selectedAdmin['_id']] : [];
		this.data.owner = this.selectedOwner && this.selectedOwner['_id'] || localStorage.token;

		this.httpService.put(this.configs.urlRequest.updateCoffeeHouse + this.item['_id'], JSON.stringify(this.data))
			.subscribe(data => {
				this.coffeeHousesService.onChanged.emit(data);
				self.toastrService.success('Updated');
				self.activeModal.close(AddedCoffeeHouseComponent);
				self.spinner = false;
			});
	}
}



