@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss'],
	// providers: [
	// 	{ provide: MAT_AUTOCOMPLETE_SCROLL_STRATEGY, deps: [Overlay], useFactory: MAT_AUTOCOMPLETE_SCROLL_STRATEGY_FACTORY },
	// 	{ provide: MAT_SELECT_SCROLL_STRATEGY, useFactory: scrollFactory, deps: [Overlay] }
	// ]
})
export class AppComponent {
	constructor(public site: SiteService, private translate: TranslateService) {
		translate.addLangs(['en', 'ru', 'ua']);
		translate.setDefaultLang('en');

		let browserLang = translate.getBrowserLang();
		translate.use(browserLang.match(/en|ua|ru/) ? browserLang : 'en');
	}
}



