import {Component, OnInit} from '@angular/core';
import {FormGroup, FormControl, Validators, FormControlName, ControlContainer} from '@angular/forms';
import {SiteService} from '../../core/services/site.service';

@Component({
	selector: 'app-contact',
	templateUrl: './contact.component.html',
	styleUrls: ['./contact.component.scss']
})

export class ContactComponent implements OnInit {
	form;

	constructor(public siteService: SiteService) {
	}

	ngOnInit() {
		this.siteService.setToolbarHeader('Contact us');
		console.log(localStorage.token);
	}

	inputAnim(e) {
		console.log(e.target.value);

		if (e.target.value) {
			e.target.nextElementSibling.className = 'input-anim';
		} else {
			e.target.nextElementSibling.className = 'input-anim-no';
		}
	}

}



