import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {SiteService} from '../../core/services/site.service';

@Component({
	selector: 'app-super-house',
	templateUrl: './super-house.component.html',
	styleUrls: ['./super-house.component.scss']
})
export class SuperHouseComponent implements OnInit {

	inputData: any;
	data: any;

	constructor(private route: ActivatedRoute, private globalData: GlobalDataService) {
	}

	ngOnInit() {
		const id = this.route.snapshot.params['id'];
		this.data = this.globalData.getGlobalData();
		this.inputData = this.data[id];
	}

}



