import {Router, ActivatedRoute, Params} from '@angular/router';
import {OnInit, Component} from '@angular/core';
import {OwnerService} from '../../core/services/owner.service';
import {FormControl, Validators} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {ToastrService} from 'ngx-toastr';

@Component({
	selector: 'app-activate-owner',
	templateUrl: './activate-owner.component.html',
	styleUrls: ['./activate-owner.component.scss']
})
export class ActivateOwnerComponent implements OnInit {
	input: any;

	nameFormControl = new FormControl('', [
		Validators.required,
		Validators.minLength(3)
	]);

	passwordFormControl = new FormControl('', [
		Validators.required,
		Validators.minLength(6)
	]);

	confirmPasswordFormControl = new FormControl('', [
		Validators.required,
		Validators.minLength(6)
	]);

	constructor(private activatedRoute: ActivatedRoute,
							private ownerService: OwnerService,
							private router: Router,
							private toastrService: ToastrService
							) {
		this.input = {
			name: '',
			confirmPassword: '',
			password: '',
			id: '',
			activationCode: ''
		};
	}

	ngOnInit() {
		this.activatedRoute.queryParams.subscribe((params: Params) => {
			console.log(params);
			this.input.activationCode = 'code' in params ? params['code'] : '';
			this.input.id = 'id' in params ? params['id'] : '';
			this.input.name = 'name' in params ? params['name'] : '';
		});
	}

	hasError() {
		return !this.nameFormControl.valid || !this.passwordFormControl.valid
			|| !this.confirmPasswordFormControl.valid ||
			this.passwordFormControl.value !== this.confirmPasswordFormControl.value;
	}

	activate(data) {
		this.ownerService.activate(data)
			.catch((error: any) => {
				if (error.message === '') {
					// this.toastrService.error('Activation code not found');
				} else {
					this.toastrService.error(error.message);
				}
				return Observable.throw(error);
			})
			.subscribe((response) => {
			this.router.navigate(['/']);
		});
	}
}



