import {Component, ElementRef, Input, OnInit, OnChanges, HostListener} from '@angular/core';
declare const google: any;
declare const googleLoaded: any;
declare const $: any;

@Component({
	selector: 'app-google-chart',
	templateUrl: './google-chart.component.html',
	styleUrls: ['./google-chart.component.scss']
})
export class GoogleChartComponent implements OnInit, OnChanges {
	showSpinner = true;

	@Input('chartType') public chartType: string;
	@Input('chartOptions') public chartOptions: any;
	@Input('chartData') public chartData: any;

	@HostListener('window:resize', ['$event'])
	onResize(event) {
		this.drawChart();
	}

	constructor(public element: ElementRef) {
	}

	ngOnInit() {
		google.charts.load('current', {'packages': ['corechart']});
		google.charts.setOnLoadCallback(this.drawChart);
	}

	ngOnChanges() {
		this.drawChart();
	}

	drawChart() {
		if (this) {
			let wrapper;
			wrapper = new google.visualization.ChartWrapper({
				chartType: this.chartType,
				dataTable: this.chartData,
				options: this.chartOptions || {},
				containerId: this.element.nativeElement.querySelector('.chart')
			});
			this.showSpinner = false;
			wrapper.draw();
		}
	}
}



