import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {CoffeeHousesService} from '../../services/coffee-houses.service';
import {Router} from '@angular/router';
import {SiteService} from '../../services/site.service';
import {MatDialog, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {Owner} from '../../models/owner';
import {SelectionModel} from '@angular/cdk/collections';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {User} from '../../models/user';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {map} from 'rxjs/operators/map';

import {startWith} from 'rxjs/operators/startWith';
import {UserService} from '../../services/user.service';
import {ConfirmDialogComponent} from '../../dialogs/confirm-dialog/confirm-dialog.component';


@Component({
	selector: 'app-house-admin-list',
	templateUrl: './house-admin-list.component.html',
	styleUrls: ['./house-admin-list.component.scss']
})
export class HouseAdminListComponent implements OnInit {
	@Input() coffeeHouse: any;

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	displayedColumns = ['avatar', 'name', 'status', 'actions'];
	dataSource = new MatTableDataSource<any>();

	selectedAdmin = {name: ''};
	stateUserCtrl: FormControl;
	filteredUserStates: Observable<any[]>;

	allUsers = [];
	allAdmins = [];
	availableUsers = [];


	constructor(private httpService: HttpService, private configs: ConfigsService,
							private coffeeHousesService: CoffeeHousesService,
							public router: Router,
							public site: SiteService,
							private modalService: NgbModal,
							private userService: UserService,
							private dialog: MatDialog) {
		this.stateUserCtrl = new FormControl();

	}


	getUsers() {
		return this.httpService.get(this.configs.urlRequest.users);
	}

	createAdminRequest() {
		if (this.stateUserCtrl.value && this.stateUserCtrl.value.hasOwnProperty('_id')) {
			const coffeeHouseId = this.coffeeHouse && this.coffeeHouse.hasOwnProperty('_id') ? this.coffeeHouse._id : '';
			const userId = this.stateUserCtrl.value && this.stateUserCtrl.value.hasOwnProperty('_id') ? this.stateUserCtrl.value : '';

			this.userService.createAdminRequest(userId, coffeeHouseId)
				.subscribe(response => {
					this.stateUserCtrl.reset('');
					this.getUserList();
				});
		}
	}

	removeArmin(user) {
		const dialogRef = this.dialog.open(ConfirmDialogComponent, {
			data: {
				header: 'Delete',
				message: 'Are you sure want to delete the user?',
				yes: 'Delete',
				no: 'Cancel'
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			const coffeeHouseId = this.coffeeHouse && this.coffeeHouse.hasOwnProperty('_id') ? this.coffeeHouse._id : '';
			const userId = user._id;

			if (result) {
				this.userService.removeAdmin(userId, coffeeHouseId).subscribe(response => {
					this.getUserList();
				});
			}
		});
	}


	updateAvailableUsers(users) {
		this.availableUsers = this.allUsers.filter(item => this.availableUserFilter(item));
		this.filteredUserStates = this.stateUserCtrl.valueChanges
			.pipe(
				startWith({} as any),
				map(user => user && typeof user === 'object' ? user['name'] : user),
				map(name => name ? this.filterUserStates(name) : this.availableUsers.slice()),
			);
	}

	filterUserStates(name: any) {
		return this.allUsers.filter(state =>
			state.name.toLowerCase().indexOf(name.toLowerCase()) === 0);
	}
	availableUserFilter(item) {
		return this.allAdmins.findIndex(element => element._id === item.id) === -1;
	}
	displayFn(user: any): string {
		return user ? user.name : user;
	}



	getAdminsWithRequest() {
		this.coffeeHousesService.getAdminsWithRequest(this.coffeeHouse._id).subscribe(data => {
			this.dataSource.data = data;
			this.allAdmins = data;

			this.updateAvailableUsers(data);
		});
	}

	getUserList() {
		this.getUsers()
			.finally(() => {
				if (this.coffeeHouse) {
				this.getAdminsWithRequest();
				}
			})
			.subscribe(response => {
				this.allUsers = response;

				this.updateAvailableUsers([]);
			});
	}


	ngOnInit() {
		this.getUserList();
	}

	applyFilter(filterValue: string) {
		filterValue = filterValue.trim(); // Remove whitespace
		filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
		this.dataSource.filter = filterValue;
	}
}



