import {Component, OnInit, OnDestroy} from '@angular/core';
import {RequestOptions, URLSearchParams} from '@angular/http';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {ConfigsService} from 'app/core/configs.service';
import {HttpService} from 'app/core/services/http.service';
import {CoffeeHousesService} from '../../core/services/coffee-houses.service';
import {SocketIOService} from '../../core/services/socket-io.service';
import {SiteService} from '../../core/services/site.service';

@Component({
	selector: 'app-house-list',
	templateUrl: './houses.component.html',
	styleUrls: ['./houses.component.scss']
})
export class HouseListComponent implements OnInit, OnDestroy {
	data = [];
	analytics: boolean;

	constructor(private configs: ConfigsService,
							private httpService: HttpService,
							private coffeeHousesService: CoffeeHousesService,
							private socketIOService: SocketIOService,
							private siteService: SiteService) {
		this.analytics = false;
		this.coffeeHousesService.onChanged.subscribe(() => this.getCoffeeHouses());

		this.socketIOService.socket.on('broadcast_newUserInCoffeeHouse', (value) => {
			const index = this.data.findIndex((item) => {
				return item._id === value.houseId;
			});

			if (index !== -1) {
				this.data[index].visitorsCount++;
			}
		});
		this.socketIOService.socket.on('broadcast_userLeaveCoffeeHouse', (value) => {
			const index = this.data.findIndex((item) => {
				return item._id === value.houseId;
			});

			if (index !== -1 && this.data[index].visitorsCount > 0) {
				this.data[index].visitorsCount--;
			}
		});
	}

	getCoffeeHouses() {
		this.coffeeHousesService.getAllForOwner().subscribe(houses => {
			this.data = houses;
		});
	}

	ngOnInit() {
		this.siteService.setToolbarHeader('Coffee Houses');
		this.getCoffeeHouses();
	}

	ngOnDestroy() {
		this.socketIOService.removeAllListeners();
	}
}



