import {Component, OnInit, NgModule} from '@angular/core';
import {Router} from '@angular/router';

import {AngularFireAuth} from 'angularfire2/auth';
import {Observable} from 'rxjs/Observable';
import * as firebase from 'firebase/app';

// import {AngularFireDatabase, FirebaseObjectObservable} from 'angularfire2/database';
import {AngularFirestore} from 'angularfire2/firestore';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {ToastrService} from 'ngx-toastr';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/do';
import {SiteService} from '../../core/services/site.service';
import {TranslateService} from '@ngx-translate/core';

const redirectToRole = function () {
	return localStorage.role === 'GLOBAL' ? 'owners' : 'houses';
};

@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

	email: string;
	user: Observable<firebase.User>;

	constructor(private afAuth: AngularFireAuth,
							private router: Router,
							private site: SiteService,
							private toastrService: ToastrService,
							private httpService: HttpService,
							private configs: ConfigsService,
							private translate: TranslateService) {
		this.user = afAuth.authState;
	}

	ngOnInit() {
		if (localStorage.role) {
			this.router.navigate(['pages/' + redirectToRole()]);
		}
	}

	login(login) {
		this.site.showProgressBar(true);
		this.afAuth.auth.signInWithEmailAndPassword(login.login.value, login.pass.value).then(function (user) {
			const userId = user.uid;

			this.httpService.login(this.configs.urlRequest.adminLogin, JSON.stringify({_id: userId}))
				.finally(() => {
					this.site.showProgressBar(false);
				})
				.subscribe(data => {
					const role = data.admin.type;

					localStorage.setItem('token', userId);
					localStorage.setItem('role', role);
					localStorage.setItem('Authorization', 'JWT ' + data.token);
					localStorage.setItem('user', JSON.stringify(data));

					if (role) {
						this.router.navigate(['pages/' + redirectToRole()]);
					}
				});
		}.bind(this)).catch(err => {
			this.site.showProgressBar(false);
			if (err.code === 'auth/user-not-found' || err.code === 'auth/wrong-password') {
				this.translate.get('text.incorrect_email_or_password')
					.subscribe((res: string) => {
						this.toastrService.error(res);
					});
			} else if (err.code === 'auth/user-disabled') {
				this.translate.get('text.account_has_been_disabled')
					.subscribe((res: string) => {
						this.toastrService.error(res);
					});
			} else {
				this.toastrService.error(err.message);
			}
		});
	}

	setNewPassword() {
		// this.setNewPass.sentPass(this.email);
	}

}



