import {Component, OnInit, ViewChild} from '@angular/core';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {CoffeeHousesService} from '../../core/services/coffee-houses.service';
import {Router} from '@angular/router';
import {SiteService} from '../../core/services/site.service';
import {MatDialog, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {Owner} from '../../core/models/owner';
import {SelectionModel} from '@angular/cdk/collections';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {CoffeeHouse} from '../../core/models/coffee-house';
import {AddedCoffeeHouseComponent} from '../../core/dialogs/added-coffee-house/added-coffee-house.component';
import {ConfirmDialogComponent} from '../../core/dialogs/confirm-dialog/confirm-dialog.component';

@Component({
	selector: 'app-super-admin-houses',
	templateUrl: './super-admin-houses.component.html',
	styleUrls: ['./super-admin-houses.component.scss']
})
export class SuperAdminHousesComponent implements OnInit {
	dataSource = new MatTableDataSource<CoffeeHouse>();

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	displayedColumns = ['select', 'avatar', 'name', 'address', 'since'];
	selection = new SelectionModel<CoffeeHouse>(false);


	constructor(private httpService: HttpService, private configs: ConfigsService,
							private coffeeHousesService: CoffeeHousesService,
							public router: Router,
							public site: SiteService,
							private modalService: NgbModal,
							private dialog: MatDialog) {
		coffeeHousesService.onChanged.subscribe(() => this.getCoffeeHouse());
	}

	getCoffeeHouse() {
		this.coffeeHousesService.getAll().subscribe(coffeeHouses => {
			this.dataSource.data = coffeeHouses;
			this.dataSource.paginator = this.paginator;
			this.dataSource.sort = this.sort;
		});
	}

	ngOnInit() {
		this.site.setToolbarHeader('item.coffee_houses');
		this.getCoffeeHouse();
	}

	removeCoffeeHouse() {
		const selected = this.selection.selected ? this.selection.selected[0] : undefined;
		if (selected) {
			const dialogRef = this.dialog.open(ConfirmDialogComponent, {
				data: {
					header: 'Delete',
					message: 'Are you sure want to delete ' + selected['name'] + ' coffee house?',
					yes: 'Delete',
					no: 'Cancel'
				}
			});

			dialogRef.afterClosed().subscribe(submit => {
				if (submit) {
					this.site.showProgressBar(true);
					this.coffeeHousesService.remove(selected['_id'])
						.finally(() => {
							this.site.showProgressBar(false);
						})
						.subscribe(house => {
						const index = this.dataSource.data.findIndex((item) => {
							return item['_id'] === house._id;
						});
						if (index !== -1) {
							this.selection.clear();
							this.dataSource.data.splice(index, 1);
							this.dataSource._updateChangeSubscription();
						}
					});
				}
			});
		}
	}

	editCoffeeHouse() {
		const selected = this.selection.selected ? this.selection.selected[0] : undefined;
		if (selected) {
			this.selection.clear();
			const modalRef = this.modalService.open(AddedCoffeeHouseComponent);
			modalRef.componentInstance.item = selected;
		}
	}

	createNewCoffeeHouse() {
		const modalRef = this.modalService.open(AddedCoffeeHouseComponent);
	}

	applyFilter(filterValue: string) {
		filterValue = filterValue.trim(); // Remove whitespace
		filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
		this.dataSource.filter = filterValue;
	}

	isItemSelected() {
		return !(this.selection.selected ? this.selection.selected.length : 0);
	}

	isAllSelected() {
		const numSelected = this.selection.selected ? this.selection.selected.length : 0;
		const numRows = this.dataSource ? this.dataSource.data.length : -1;
		return numSelected === numRows;
	}

	masterToggle() {
		if (this.isAllSelected()) {
			this.selection.clear();
		} else if (this.dataSource) {
			this.dataSource.data.forEach(row => this.selection.select(row));
		}
	}
}



