import {AfterViewInit, Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {DataSource, SelectionModel} from '@angular/cdk/collections';
import {MatDialog, MatSort, MatTableDataSource} from '@angular/material';
import {Observable} from 'rxjs/Observable';

import {ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material';
import {Owner} from 'app/core/models/owner';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {AddedCoffeeHouseComponent} from 'app/core/dialogs/added-coffee-house/added-coffee-house.component';
import {SiteService} from '../../core/services/site.service';
import {AdminService} from '../../core/services/admin.service';
import {ConfirmDialogComponent} from '../../core/dialogs/confirm-dialog/confirm-dialog.component';
import {ToastrService} from 'ngx-toastr';
import {AddedOwnerComponent} from '../../core/dialogs/added-owner/added-owner.component';
import {OwnerService} from '../../core/services/owner.service';
import {TranslateService} from '@ngx-translate/core';

@Component({
	selector: 'app-all-owners',
	templateUrl: './all-owners.component.html',
	styleUrls: ['./all-owners.component.scss']
})

export class AllOwnersComponent implements OnInit, AfterViewInit {
	private authHeader: any;
	dataSource = new MatTableDataSource<Owner>();

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	displayedColumns = ['select', 'avatar', 'name', 'email', 'since', 'control'];
	selection = new SelectionModel<Owner>(false);

	ngAfterViewInit() {
	}

	constructor(private httpService: HttpService,
							private configs: ConfigsService,
							public router: Router,
							private activatedRoute: ActivatedRoute,
							private modalService: NgbModal,
							private site: SiteService,
							private admin: AdminService,
							private ownerService: OwnerService,
							private dialog: MatDialog,
							private toastrService: ToastrService) {
	}

	getAllOwners() {
		this.admin.getAllOwners().subscribe(data => {
			this.dataSource.data = data;
			this.dataSource.paginator = this.paginator;
			this.dataSource.sort = this.sort;
		});
	}


	ngOnInit() {
		this.site.setToolbarHeader('item.coffee_house_owners');
		this.authHeader = localStorage.getItem('Authorization');
		this.getAllOwners();
	}

	editSelectedOwner() {
		const selected = this.selection.selected ? this.selection.selected[0] : undefined;
		if (selected) {
			const dialogRef = this.dialog.open(AddedOwnerComponent, {
				data: selected
			});

			dialogRef.afterClosed().subscribe(data => {
				if (data) {
					this.getAllOwners();
				}
			});
		}
	}

	removeSelectedOwner() {
		const selected = this.selection.selected ? this.selection.selected[0] : undefined;
		if (selected) {
			const dialogRef = this.dialog.open(ConfirmDialogComponent, {
				data: {
					header: 'Delete',
					message: 'Are you sure want to delete ' + selected['name'] + '?',
					yes: 'Delete',
					no: 'Cancel'
				}
			});

			dialogRef.afterClosed().subscribe(submit => {
				if (submit) {
					this.site.showProgressBar(true);
					this.ownerService.removeOwner(selected['_id'])
						.finally(() => {
							this.site.showProgressBar(false);
						})
						.subscribe(house => {
							const index = this.dataSource.data.findIndex((item) => {
								return item['_id'] === selected['_id'];
							});
							if (index !== -1) {
								this.selection.clear();
								this.dataSource.data.splice(index, 1);
								this.dataSource._updateChangeSubscription();
							}
						});
				}
			});
		}
	}

	blockOwner(element) {
		this.site.showProgressBar(true);
		this.ownerService.blockOwner(element._id)
			.finally(() => {
				this.site.showProgressBar(false);
			})
			.subscribe(owner => {
			const index = this.dataSource.data.findIndex((item) => {
				return item['_id'] === owner._id;
			});
			if (index !== -1) {
				this.dataSource.data[index] = owner;
				this.dataSource._updateChangeSubscription();
			}
		});
	}

	unblockOwner(element) {
		this.site.showProgressBar(true);
		this.ownerService.unblockOwner(element._id)
			.finally(() => {
				this.site.showProgressBar(false);
			})
			.subscribe(owner => {
			const index = this.dataSource.data.findIndex((item) => {
				return item['_id'] === owner._id;
			});
			if (index !== -1) {
				this.dataSource.data[index] = owner;
				this.dataSource._updateChangeSubscription();
			}
		});
	}

	resetPassword(element) {
		this.site.showProgressBar(true);
		this.admin.forgotPassword(element.email)
			.finally(() => {
				this.site.showProgressBar(false);
			})
			.subscribe(result => {
			this.toastrService.success(result.message);
		});
	}

	createNewOwner() {
		const modalRef = this.dialog.open(AddedOwnerComponent, {});

		modalRef.afterClosed().subscribe(data => {
			if (data) {
				this.getAllOwners();
			}
		});
	}

	applyFilter(filterValue: string) {
		filterValue = filterValue.trim(); // Remove whitespace
		filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
		this.dataSource.filter = filterValue;
	}

	isItemSelected() {
		return !(this.selection.selected ? this.selection.selected.length : 0);
	}

	isAllSelected() {
		const numSelected = this.selection.selected ? this.selection.selected.length : 0;
		const numRows = this.dataSource ? this.dataSource.data.length : -1;
		return numSelected === numRows;
	}

	masterToggle() {
		if (this.isAllSelected()) {
			this.selection.clear();
		} else if (this.dataSource) {
			this.dataSource.data.forEach(row => this.selection.select(row));
		}
	}
}



