import {Injectable} from '@angular/core';
import * as io from 'socket.io-client';
import {ConfigsService} from '../configs.service';

@Injectable()
export class SocketIOService {
	public socket;

	constructor(private configs: ConfigsService) {
		const token = localStorage.getItem('Authorization');
		this.socket = io.connect(window.location.hostname + ':8030', {
			query: 'token=' + token.slice(4, token.length)
		});
	}

	inCoffeeHouse(id: string) {
		this.socket.emit('inCoffeeHouse', id);
	}

	leaveCoffeeHouse(id: string) {
		this.socket.emit('leaveCoffeeHouse', id);
	}

	removeAllListeners() {
		this.socket.removeAllListeners();
	}
}



