import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
	selector: 'app-map-point-selector',
	templateUrl: './map-point-selector.component.html',
	styleUrls: ['./map-point-selector.component.scss']
})
export class MapPointSelectorComponent implements OnInit {
	@Output() change: EventEmitter<any> = new EventEmitter<any>();
	@Input() latitude: number;
	@Input() longitude: number;

	markerDragEnd($event: any) {
		console.log('dragEnd', $event.coords);
		this.latitude = $event.coords.lat;
		this.longitude = $event.coords.lng;
		this.change.emit([$event.coords.lat, $event.coords.lng]);
	}

	constructor() {
	}

	ngOnInit() {}
}



