import {EventEmitter, Injectable} from '@angular/core';
import {HttpService} from './http.service';
import {ConfigsService} from '../configs.service';
import {Headers, RequestOptions, URLSearchParams} from '@angular/http';
import {CoffeeHouse} from '../models/coffee-house';
import {Observable} from 'rxjs/Observable';
import {dataURItoBlob} from '../helpers/image';

@Injectable()
export class CoffeeHousesService {
	public onChanged: EventEmitter<any>;

	constructor(private httpService: HttpService,
							private configs: ConfigsService) {
		this.onChanged = new EventEmitter<any>();
	}

	getVisitors(id: string) {
		return this.httpService.get(this.configs.urlRequest.updateCoffeeHouse + id + '/visitors');
	}

	getAllForOwner() {
		const searchParams = new URLSearchParams();
		searchParams.set('owner', localStorage.token);

		const headers = new Headers({
			'Content-Type': 'application/json',
			'Authorization': localStorage.getItem('Authorization')
		});

		const options = new RequestOptions({
			headers: headers,
			search: searchParams
		});

		return this.httpService.get(this.configs.urlRequest.getCoffeeHouse, options);
	}

	getAll(): Observable<CoffeeHouse[]> {
		return this.httpService.get(this.configs.urlRequest.getCoffeeHouse, {
			headers: {
				'Authorization': localStorage.getItem('Authorization')
			}
		});
	}

	uploadImage(data) {
		const formData = new FormData();
		formData.append('image', dataURItoBlob(data));
		return this.httpService.post(this.configs.urlRequest.updateCoffeeHouse + '/upload', formData,{
			'Authorization': localStorage.getItem('Authorization')
		});
	}

	getAdmins(id): Observable<any[]> {
		return this.httpService.get(this.configs.urlRequest.updateCoffeeHouse + id + '/admins');
	}

	getAdminsWithRequest(id): Observable<any[]> {
		return this.getAdmins(id).map(data => {
			const value = data['admins'].map(item => Object.assign(item, {itemType: 'admin'}));
			return value.concat(data['requests'].map(item => Object.assign(item, {itemType: 'request'})));
		});
	}

	remove(id) {
		return this.httpService.delete(this.configs.urlRequest.updateCoffeeHouse + id);
	}
}



