import {Component, Input, OnInit} from '@angular/core';
import {LogoutService} from 'app/core/services/logout.service';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {TriggerDashboardService} from 'app/core/services/trigger-dashboard.service';
import {ConfirmDialogComponent} from '../../dialogs/confirm-dialog/confirm-dialog.component';
import {MatDialog} from '@angular/material';
import {EditProfilePopupComponent} from '../../modals/edit-profile-popup/edit-profile-popup.component';


@Component({
	selector: 'app-user-left-bar',
	templateUrl: './user-left-bar.component.html',
	styleUrls: ['./user-left-bar.component.scss']
})
export class UserLeftBarComponent implements OnInit {
	avatarUrl: string;
	fullName: string;
	userType: string;
	id: string;
	mode: string;

	constructor(private logout: LogoutService,
							private globalDataService: GlobalDataService,
							public triggerDashboardService: TriggerDashboardService,
							private dialog: MatDialog) {
		this.mode = 'side'; // 'over' | 'push' | 'side'
	}

	ngOnInit() {
		this.uploadUser();
		this.globalDataService.change.subscribe(() => this.uploadUser());
	}

	editProfile() {
		const dialogRef = this.dialog.open(EditProfilePopupComponent, {
			data: {
				id: this.id,
				name: this.fullName,
				avatarUrl: this.avatarUrl
			}
		});

		dialogRef.afterClosed().subscribe(() => {
			this.globalDataService.refreshUserData();
		});
	}

	uploadUser() {
		const userData = this.globalDataService.getUserData();
		this.fullName = userData['admin']['name'] || '';
		this.id = userData['admin']['id'] || '';
		this.userType = userData['admin']['type'] || '';
		this.avatarUrl = userData['admin']['avatarUrl'] || '';
	}

	closePopup(popupOpened: boolean): void {
	}

	logoutFunc() {
		this.logout.logout();
	}
}



