import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {RequestOptions, URLSearchParams} from '@angular/http';

import {GlobalDataService} from 'app/core/services/global-data.service';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';
import {SocketIOService} from '../../core/services/socket-io.service';
import {CoffeeHousesService} from '../../core/services/coffee-houses.service';
import {HttpParams} from '@angular/common/http';
import {SiteService} from '../../core/services/site.service';

@Component({
	selector: 'app-owner',
	templateUrl: './owner.component.html',
	styleUrls: ['./owner.component.scss', '../houses/houses.component.scss']
})
export class OwnerComponent implements OnInit, OnDestroy {
	houses = [];
	analytics: boolean;

	constructor(private router: Router,
							private activatedRoute: ActivatedRoute,
							private globalData: GlobalDataService,
							private httpService: HttpService,
							private socketIOService: SocketIOService,
							private configs: ConfigsService,
							private coffeeHousesService: CoffeeHousesService,
							private site: SiteService) {
		this.analytics = false;

		this.coffeeHousesService.onChanged.subscribe(() => {
			this.getCoffeeHouse();
		});
	}

	getCoffeeHouse() {
		const searchParams = new URLSearchParams();
		this.activatedRoute.params.subscribe((params: Params) => {

			for (const item in params) {
				searchParams.set(item, params[item]);
			}

			const options = {
				headers: this.configs.headersDefault,
				search: searchParams
			};

			this.httpService.get(this.configs.urlRequest.getCoffeeHouse, options)
				.subscribe(response => {
					this.houses = response;
				});
		});
	}

	ngOnInit() {
		this.site.setToolbarHeader('item.owner_coffee_houses');
		this.getCoffeeHouse();

		this.socketIOService.socket.on('broadcast_newUserInCoffeeHouse', (data) => {
			const index = this.houses.findIndex((item) => {
				return item._id === data.houseId;
			});

			if (index !== -1) {
				this.houses[index].visitorsCount++;
			}
		});
		this.socketIOService.socket.on('broadcast_userLeaveCoffeeHouse', (data) => {
			const index = this.houses.findIndex((item) => {
				return item._id === data.houseId;
			});

			if (index !== -1 && this.houses[index].visitorsCount > 0) {
				this.houses[index].visitorsCount--;
			}
		});
	}

	ngOnDestroy() {
		this.socketIOService.socket.removeAllListeners();
	}
}



