import {Component, OnInit, OnDestroy} from '@angular/core';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {Router} from '@angular/router';
import {HttpService} from '../../core/services/http.service';
import {ConfigsService} from '../../core/configs.service';
import {CoffeeHousesService} from '../../core/services/coffee-houses.service';
import {SocketIOService} from '../../core/services/socket-io.service';
import {SiteService} from '../../core/services/site.service';

@Component({
	selector: 'app-analytics',
	templateUrl: './analytics.component.html',
	styleUrls: ['./analytics.component.scss']
})

export class AnalyticsComponent implements OnInit, OnDestroy {
	data: any;
	analytics: boolean;
	name: any;
	isChecked = {};
	idCard: any;

	constructor(private globalData: GlobalDataService,
							private router: Router,
							private httpService: HttpService,
							private configs: ConfigsService,
							private coffeeHousesService: CoffeeHousesService,
							private socketIOService: SocketIOService,
							private siteService: SiteService) {
		this.analytics = true;
		this.coffeeHousesService.onChanged.subscribe(() => this.getCoffeeHouse());
		this.socketIOService.socket.on('broadcast_newUserInCoffeeHouse', (value) => {
			const index = this.data.findIndex((item) => {
				return item._id === value.houseId;
			});

			if (index !== -1) {
				this.data[index].visitorsCount++;
			}
		});
		this.socketIOService.socket.on('broadcast_userLeaveCoffeeHouse', (value) => {
			const index = this.data.findIndex((item) => {
				return item._id === value.houseId;
			});

			if (index !== -1 && this.data[index].visitorsCount > 0) {
				this.data[index].visitorsCount--;
			}
		});
	}

	ngOnInit() {
		this.siteService.setToolbarHeader('item.analytics');
		this.getCoffeeHouse();
	}

	ngOnDestroy() {
		this.socketIOService.removeAllListeners();
	}


	getCoffeeHouse() {
		this.coffeeHousesService.getAll().subscribe(data => {
			this.data = data;
		});
	}

	checkedHouses(ev) {
		console.log('Houses', ev);
		this.idCard = ev;
	}

	gooAnalyticsDetail(house) {
		console.log(house);
		if (this.idCard) {
			console.log('IdCardds', this.idCard);
			this.router.navigate(['pages/analytics/detail', {idCards: JSON.stringify(this.idCard)}]);
		} else {
			this.router.navigate(['pages/analytics/detail']);
		}
	}
}



