import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LoginComponent} from './pages/login/login.component';
import {GuardRoutingGuard} from './core/guards/guard-routing.guard';
import {HouseListComponent} from './pages/houses/houses.component';
import {AnalyticsComponent} from './pages/analytics/analytics.component';
import {AnalyticsDetailComponent} from './pages/analytics-detail/analytics-detail.component';
import {PaymentComponent} from './pages/payment/payment.component';
import {ContactComponent} from './pages/contact/contact.component';
import {HouseEditComponent} from './pages/house-edit/house-edit.component';
import {SuperAdminHousesComponent} from './pages/super-admin-houses/super-admin-houses.component';
import {SuperAdminUsersComponent} from './pages/super-admin-users/super-admin-users.component';
import {SuperAdminPayplanComponent} from './pages/super-admin-payplan/super-admin-payplan.component';
import {SuperHouseComponent} from './pages/super-house/super-house.component';
import {AngularFireModule} from 'angularfire2';
import {environment} from 'environments/environment';
import {PagesComponent} from 'app/pages/pages.component';
import {SuperAdminOwnersComponent} from 'app/pages/super-admin-owners/super-admin-owners.component';
import {AllOwnersComponent} from 'app/pages/all-owners/all-owners.component';
import {OwnerComponent} from 'app/pages/owner/owner.component';
import {ForgotPasswordComponent} from 'app/pages/forgot-password/forgot-password.component';
import {ResetPasswordComponent} from './pages/reset-password/reset-password.component';
import {ActivateOwnerComponent} from './pages/activate-owner/activate-owner.component';

const redirectToRole = function () {
	if (localStorage.role === 'GLOBAL') {
		return 'owners';
	} else if (localStorage.role === 'OWNER') {
		return 'houses';
	} else {
		return '';
	}
};

const routes: Routes = [
	{ path: 'login', component: LoginComponent, pathMatch: 'full'},
	{ path: 'forgot-password', component: ForgotPasswordComponent, pathMatch: 'full'},
	{ path: 'reset-password', component: ResetPasswordComponent, pathMatch: 'full'},
	{ path: 'activate-owner', component: ActivateOwnerComponent, pathMatch: 'full'},
	{
		path: 'pages',
		component: PagesComponent,
		canActivate: [GuardRoutingGuard],
		children: [
			{
				path: 'houses',
				component: HouseListComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'houses/:id',
				component: HouseEditComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'houses-list',
				component: SuperAdminHousesComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'analytics',
				component: AnalyticsComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'analytics/detail',
				component: AnalyticsDetailComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'payment',
				component: PaymentComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'contact',
				component: ContactComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'owners',
				component: SuperAdminOwnersComponent,
				canActivate: [GuardRoutingGuard],

				children: [
					{
						path: 'all',
						component: AllOwnersComponent
					},
					{
						path: 'owner',
						component: OwnerComponent
					},
					{
						path: '**',
						redirectTo: 'all'
					}
				]
			},
			{
				path: 'users',
				component: SuperAdminUsersComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'payplan',
				component: SuperAdminPayplanComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: 'houses/:id',
				component: SuperHouseComponent,
				canActivate: [GuardRoutingGuard]
			},
			{
				path: '**',
				redirectTo: './login'
			}
		]
	},
	{
		path: '**',
		redirectTo: 'login'
	}
];

@NgModule({
	imports: [
		RouterModule.forRoot(
			routes,
			// { enableTracing: true }
		),
		AngularFireModule.initializeApp(environment.firebase, 'AppModule'),
	],
	exports: [RouterModule]
})
export class AppRoutingModule {
}



