import {Component, OnInit, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {FormBuilder, FormControl, FormGroup, FormGroupDirective, NgForm, Validators} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';

import {AdminService} from '../../services/admin.service';
import {OwnerService} from '../../services/owner.service';
import {Observable} from 'rxjs/Observable';
import {ToastrService} from 'ngx-toastr';
import {SiteService} from '../../services/site.service';

@Component({
	selector: 'app-added-owner',
	templateUrl: './added-owner.component.html',
	styleUrls: ['./added-owner.component.scss']
})
export class AddedOwnerComponent implements OnInit {
	ownerForm: FormGroup;

	createForm() {
		this.ownerForm = this.fb.group({
			_id: '',
			name: ['', Validators.compose([
				Validators.required,
				Validators.minLength(1),
				Validators.maxLength(35),
				//NoWhitespaceValidator
			])],
			email: ['', Validators.compose([
				Validators.required,
				Validators.email,
				Validators.maxLength(35),
				//NoWhitespaceValidator
			])],
		});
	}

	constructor(public dialogRef: MatDialogRef<AddedOwnerComponent>,
							public admin: AdminService,
							public site: SiteService,
							public toastrService: ToastrService,
							public ownerService: OwnerService,
							private fb: FormBuilder,
							@Inject(MAT_DIALOG_DATA) public data: any) {
		this.createForm();
		if (this.isEditMode()) {
			this.ownerForm.patchValue(this.data);
		}
	}

	onNoClick(): void {
		this.dialogRef.close();
	}

	isEditMode() {
		return !!this.data;
	}

	submit(value) {
			this.site.showProgressBar(true);

			const request = this.isEditMode() ?
				this.ownerService.updateOwner(value) :
				this.ownerService.createOwner(value.email, value.name);

			request.finally(() => {
				this.site.showProgressBar(false);
			})
				.catch((error: any) => {
					if (error.message === 'auth/invalid-email') {
						this.toastrService.error('Invalid email address.');
					} else if (error.message === 'already-exist') {
						this.toastrService.error('Email already exist.');
					} else {
						this.toastrService.error(error.message);
					}
					return Observable.throw(error);
				}).subscribe(owner => {
				this.dialogRef.close(owner);
			});
	}

	ngOnInit() {
	}
}



