import {Component, OnInit, Input} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {GlobalDataService} from 'app/core/services/global-data.service';

@Component({
	selector: 'app-house-edit',
	templateUrl: './house-edit.component.html',
	styleUrls: ['./house-edit.component.scss']
})
export class HouseEditComponent implements OnInit {

	inputData: any;
	data: any;
	tabChecker = true;

	@Input() popupOpened: boolean;
	@Input() popupOpened2: boolean;

	closeResult: string;
	menuOpened: false;


	constructor(private route: ActivatedRoute, private globalData: GlobalDataService) {
	}

	ngOnInit() {
		const id = this.route.snapshot.params['id'];

		this.data = this.globalData.getGlobalData();

		this.inputData = this.data[id];

		console.log(this.inputData);
	}

	closePopup(popupOpened: boolean, popupOpened2: boolean): void {
		this.popupOpened = popupOpened;
		this.popupOpened2 = popupOpened2;
	}
}



