import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
	name: 'statusCaffee'
})
export class StatusCaffeePipe implements PipeTransform {

	transform(value: any, args?: any): any {
		if (value === 'ONLINE') {
			return 'Online';
		}
		if (value === 'OFFLINE') {
			return 'Offline';
		}
		if (value === 'DEBTOR') {
			return 'Ower';
		}

		return value;
	}

}



