import {Component, OnInit, Output, EventEmitter, ViewChild, Input, Inject} from '@angular/core';
import * as firebase from 'firebase/app';
import {ImageCropperComponent, CropperSettings, Bounds} from 'ng2-img-cropper';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {ConfirmDialogComponent} from '../../dialogs/confirm-dialog/confirm-dialog.component';
import {AdminService} from '../../services/admin.service';
import { toDataURL } from '../../helpers/image';
import {GlobalDataService} from '../../services/global-data.service';

@Component({
	selector: 'app-profile-edit-popup',
	templateUrl: './edit-profile-popup.component.html',
	styleUrls: ['./edit-profile-popup.component.scss']
})
export class EditProfilePopupComponent implements OnInit {
	newName: string;
	newAvatar: any;
	newFile: any;

	name: string;
	imageModel: any;
	cropperSettings: CropperSettings;
	croppedWidth: number;
	croppedHeight: number;

	@ViewChild('cropper', undefined) cropper: ImageCropperComponent;

	@Output()
	closed: EventEmitter<boolean> = new EventEmitter<boolean>();

	@Input()
	fullName: string;

	popupOpened: false;


	constructor(public dialogRef: MatDialogRef<ConfirmDialogComponent>,
							private adminService: AdminService,
							private globalDataService: GlobalDataService,
							@Inject(MAT_DIALOG_DATA) public data: any) {
		this.name = 'Angular2';

		this.cropperSettings = new CropperSettings();
		this.cropperSettings.width = 200;
		this.cropperSettings.height = 200;
		this.cropperSettings.keepAspect = false;

		this.cropperSettings.croppedWidth = 200;
		this.cropperSettings.croppedHeight = 200;

		this.cropperSettings.canvasWidth = 500;
		this.cropperSettings.canvasHeight = 300;

		this.cropperSettings.minWidth = 100;
		this.cropperSettings.minHeight = 100;

		this.cropperSettings.rounded = true;
		this.cropperSettings.minWithRelativeToResolution = false;

		this.cropperSettings.cropperDrawSettings.strokeColor = 'rgba(255,255,255,1)';
		this.cropperSettings.cropperDrawSettings.strokeWidth = 2;

		this.imageModel = {};
	}

	ngOnInit() {
		this.newName = this.data.name;
		toDataURL(this.data.avatarUrl, (url) => {
			const image = new Image();
			image.src = url;
			this.cropper.setImage(image);
		});
	}

	cropped(bounds) {


		this.croppedHeight = bounds.bottom - bounds.top;
		this.croppedWidth = bounds.right - bounds.left;
		this.newAvatar = this.imageModel.image;
		this.newFile = this.imageModel.image;
		const element = (document.querySelectorAll('input[type=file]')[0] as HTMLInputElement);
		const fullFileName = element.value;
		const index = fullFileName.lastIndexOf('\\');
		this.newAvatar = fullFileName.slice(index + 1, fullFileName.length);

	}

	sendNewData($event) {
		$event.preventDefault();

		this.adminService.uploadImage(this.newFile).toPromise()
			.then((url) => {
				const token = window.localStorage.token;
				const db = firebase.database().ref('/users/' + token);

				this.adminService.update(this.data.id, { name: this.newName, avatarUrl: url }).subscribe((admin) => {
					this.globalDataService.refreshUserData();
				});

				db.update({fullName: this.newName});


				const index = this.newFile.indexOf(',');
				const uploadingData = this.newFile.slice(index + 1, this.newFile.length);
				this.closePopup();
			});
	}

	closePopup(): void {
		this.dialogRef.close();
	}

}



