@Component({
	selector: 'app-super-admin-users',
	templateUrl: './super-admin-users.component.html',
	styleUrls: ['./super-admin-users.component.scss']
})
export class SuperAdminUsersComponent implements OnInit, OnDestroy {
	dataSource: any;
	coffeeHouseId: string;

	displayedColumns = ['avatar', 'name', 'since', 'status', 'control'];

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	constructor(private httpService: HttpService,
							public config: ConfigsService,
							private activatedRoute: ActivatedRoute,
							private toastrService: ToastrService,
							private socketIOService: SocketIOService,
							private coffeeHousesService: CoffeeHousesService,
							private userService: UserService,
							public dialog: MatDialog,
							private site: SiteService) {
	}

	createAdminRequest(userId) {
		this.userService.createAdminRequest(userId, this.coffeeHouseId).subscribe(response => {
			const index = this.dataSource.data.findIndex((item) => {
				return item._id === userId;
			});

			if (index !== -1) {
				this.dataSource.data[index].adminRequestsCount++;
			}
			this.toastrService.success('Request sent!');
		});
	}

	removeArmin(user) {
		const dialogRef = this.dialog.open(ConfirmDialogComponent, {
			data: {
				header: 'Delete',
				message: 'Are you sure want to delete the user?',
				yes: 'Delete',
				no: 'Cancel'
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			if (result) {
				this.userService.removeAdmin(user._id, this.coffeeHouseId).subscribe(response => {
					const index = this.dataSource.data.findIndex((item) => {
						return item._id === user._id;
					});

					if (index !== -1) {
						this.dataSource.data[index].adminInCoffeeHouses = response.adminInCoffeeHouses;
						this.dataSource.data[index].adminRequestsCount = response.adminRequestsCount || 0;
					}
				});
			}
		});
	}

	ngOnInit() {
		this.site.setToolbarHeader('');
		this.activatedRoute.params.subscribe((params: Params) => {
			if (params.id) {
				this.coffeeHouseId = params.id;
				this.socketIOService.inCoffeeHouse(params.id);  // '59c9d506ce0e011b6d53d0c9'

				this.coffeeHousesService.getVisitors(params.id).subscribe((visitors) => {
					this.dataSource = new MatTableDataSource<User>(visitors); // TEST_ITEMS
					this.dataSource.paginator = this.paginator;
					this.dataSource.sort = this.sort;

					this.socketIOService.socket.on('newUserInCoffeeHouse', (user) => {
						console.log('newUserInCoffeeHouse: ', user);
						this.dataSource.data.push(user);
					});
					this.socketIOService.socket.on('userLeaveCoffeeHouse', (id) => {
						const index = this.dataSource.data.findIndex((item) => {
							return item._id === id;
						});

						if (index !== -1) {
							this.dataSource.data.splice(index, 1);
						}
					});
				});
			}
		});
	}

	ngOnDestroy() {
		this.socketIOService.socket.removeAllListeners();
		this.socketIOService.leaveCoffeeHouse(this.coffeeHouseId);
	}

	applyFilter(filterValue: string) {
		filterValue = filterValue.trim(); // Remove whitespace
		filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
		this.dataSource.filter = filterValue;
	}

	adminInCoffeeHouse(user: any, id: string) {
		if (!user.adminInCoffeeHouses) {
			return false;
		}
		for (let i = 0; i < user.adminInCoffeeHouses.length; i++) {
			if (user.adminInCoffeeHouses[i] === this.coffeeHouseId) {
				return true;
			}
		}
		return false;
	}
}



