import {Component, Input, OnInit, EventEmitter, Output} from '@angular/core';
import {Router} from '@angular/router';
import {AddedCoffeeHouseComponent} from '../../dialogs/added-coffee-house/added-coffee-house.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
	selector: 'app-house-item',
	templateUrl: './house-item.component.html',
	styleUrls: ['./house-item.component.scss']
})

export class HouseItemComponent implements OnInit {
	@Input() addNew = false;
	@Input() inputData: any;
	@Input() analytics: boolean;
	@Input('idCards') idCards: any = [];
	isChecked = {};
	@Output() checkedHouses = new EventEmitter();


	ngOnInit() {
	}

	constructor(public router: Router, private modalService: NgbModal) {
	}

	addNewCoffeeHouse() {
		const modalRef = this.modalService.open(AddedCoffeeHouseComponent);
	}

	navigateToUsersList(house) {
		// [routerLink]="['../../users', {id: item._id}]"
		console.log('role', this.router.url);
		this.router.navigate(['pages/users', {id: house._id}]);
	}

	onCheck(e, el) {
		console.log(e);
		if (this.isChecked[el._id]) {
			this.isChecked = {};
		} else {
			this.isChecked = {};
			this.isChecked[el['_id']] = el;
		}
		this.checkedHouses.emit(Object.values(this.isChecked)[0]);
	}
}



